<template>
    <div class="safe_view" style="width:1000px">
        <el-button  class="back" @click="$router.back()">返回列表</el-button>
        <div class="view clearfix"  ref="img_box" v-if="item">
            <div class="left" >
                <!-- tab -->
                <div class="img_box" @mousemove="move" @mouseover="flag=true" @mouseout="flag=false">
                <div class="jing" v-show="flag"  :style="{left:left-100+'px',top:top-100+'px'}" ></div>
                    <img :src="mid_img" alt />
                </div>
                <div class="big_img_box" v-show="flag">
                    <img :src="mid_img" :style="{left:-left*2+200+'px',top:-top*2+200+'px'}" alt />
                </div>
                <div class="img_btns clearfix">
                    <img
                        v-for="x in item.imgs"
                        :class="{con:x==mid_img}"
                        :key="x"
                        :src="x"
                        @click="mid_img=x"
                        alt
                    />
                </div>
            </div>
            <div class="left right">
                <h2 class="title">{{item.title}}</h2>
                <p class="supplier">{{item.supplier}}</p>
                <p class="price">￥{{item.price}}</p>
                <el-button type="danger" @click="add()">添加到购物车</el-button>
            </div>
        </div>
        <div class="same">
            <h4>相似商品推荐</h4>
            <list class="list" :list="same_list"></list>
        </div>
    </div>
</template>
<script>
import $ajax from "../plugins/ajax";
export default {
    beforeRouteEnter: async function(to, from, next) {
        var res = await $ajax("/detail?goodId=" + to.params.goodId);
        next(vm => {
            vm.item = res.data[0];
            vm.item.imgs = JSON.parse(vm.item.imgs);
            vm.mid_img = vm.item.imgs[0];
        });
    },
    beforeRouteUpdate:async function(to,from,next) {
        var res = await $ajax("/detail?goodId=" + to.params.goodId);
        this.item = res.data[0];
        this.item.imgs = JSON.parse(this.item.imgs);
        this.mid_img =this.item.imgs[0];
        this.$store.commit("loading",false);
        next();
    },
    data() {
        return {
            item: null,
            mid_img: "",
            left: 0,
            top: 0,
            flag: false,
            flag2:false,
            same_list:[]
        };
    },
    watch:{
        item:async function({type_one,type_two,Id}){
           
            this.same_list=[];
            var res=await this.$ajax("/goodList?type_one="+type_one+"&type_two="+type_two);
            res.data.forEach((item,index)=>{
                if(item.Id==Id){
                    res.data.splice(index,1);
                }
            })
            this.same_list=res.data.slice(0,10)
        }
    },
    computed: {
        value() {
            return parseInt(this.item.nice) % 10;
        }
    },
    methods: {
        move(e) {
            var x=e.clientX;
            var y=e.clientY;
            var st=document.body.scrollTop||document.documentElement.scrollTop
            var ot=this.$refs.img_box.offsetTop;
            var ol=this.$refs.img_box.offsetLeft;
            this.left = x - ol 
            this.top = y - ot+st ;
       
                if (this.left < 100) {
                    this.left = 100
                }
                if (this.left > 300) {
                    this.left = 300
                }
                if (this.top < 100) {
                    this.top= 100
                }
                if (this.top > 300) {
                    this.top = 300
                }

            
        },
         add() {
            var token = this.$store.state.token;
            if (token) {
               
                (async () => {
                    var res = await this.$ajax(
                        "/add?goodId=" + this.item.Id + "&token=" + token
                    );
                    var code = res.data.code;
                    if (code == 0) {
                        this.$message({
                            message: "登录过期，即将跳转到登录页",
                            type: "warning"
                        });
                        setTimeout(() => {
                            this.$router.push(
                                "/login?url=" + this.$route.fullPath
                            );
                        }, 2000);
                    } else if (code == 1) {
                        this.$message({
                            type:"success",
                            message:"添加成功"
                        });
                    }else{
                        this.$message.error('添加失败,请稍后再试');
                    }
                })();
            } else {
                this.$message({
                    message: "您还没有登录，即将跳转到登录页",
                    type: "warning"
                });
                setTimeout(() => {
                    this.$router.push("/login?url=" + this.$route.fullPath);
                }, 2000);
            }
        }
    }
};
</script>
<style scoped>
.list{
    width:1000px;
}
.list >>> li{
    width:180px;
    margin:5px;
    padding:5px;
}
.list >>> img{
    height:180px;

}

.same{
    margin-top:20px
}
.same{
    color: #999;
    padding-bottom: 10px;
    border-bottom: 1px solid #dedede;
}

img {
    display: block;
}
.back {
    margin-top: 10px;
}
.img_box {
    width: 400px;
    position: relative;
}
.img_box img {
    width: 100%;
    border-radius: 10px;
}
.img_btns img {
    width: 80px;
    float: left;
    box-sizing: border-box;
    padding: 2px
   
}
 .img_box .jing{
            width: 200px;
            height: 200px;
            position: absolute;
            background: rgba(0, 0, 0, 0.3);
            cursor: all-scroll;
        }
.img_btns .con {
        transform: translateY(-2px);
        box-shadow: 0 10px 10px rgba(0,0,0,0.3);
}
.big_img_box {
    position: absolute;
    top: 0;
    left: 400px;
    width: 400px;
    height: 400px;
    overflow: hidden;
   z-index: 10;
}
.big_img_box img {
    width: 800px;
    position: absolute;
    left: 0;
    top: 0;
}
.left {
    float: left;
    position: relative;
}
.right {
    margin: 80px 0 0 50px;
}
.right p {
    margin: 20px 0;
}

</style>